import axios from 'axios';
import Qs from "qs";
import {Toast } from 'antd-mobile'

//请求 头contentTpye类型
const contentTpyeArrs = [
    "application/json",
    "application/x-www-form-urlencoded;charset=UTF-8",
    "multipart/form-data"
]

//基本设置
let options = {
    baseURL: "",
    timeout: 10000,
    headers: {
        post: {
            'Content-Type': contentTpyeArrs[0]
        }
    }
}

const ajax = axios.create(options);

let defaultContentType = contentTpyeArrs[0];

ajax.interceptors.request.use((config) => {
    return config;
},(err) => {
    return Promise.reject(err);
})

ajax.interceptors.response.use((response) => {
    // console.log(response)
    if (response.status === 200) {
        return response.data
    } else {
        return response
    }
}, function (error) {
    // 对响应错误做点什么
    if (error && error.response) {
        Toast.info(error.response.statusText, 5)
        return Promise.reject(error);
    } else {
        Toast.info("请求超时");
        return Promise.reject(error);
    }
})

const getAction = function (url, data={}) {
    return ajax.get(url, {
        params: data
    });
}

const postAction = function (url, data={}, contentTpyeIndex=0) {
	// 判断当前请求类型，确定向后端传递数据的方式,前后端沟通后按约定改动
    let _contentTpye = contentTpyeArrs[contentTpyeIndex] || defaultContentType;
    let _data;
	
	if (_contentTpye === contentTpyeArrs[1]) {
        _data = Qs.stringify(data)
	} else if (_contentTpye === contentTpyeArrs[0]){
        _data = JSON.stringify(data)
	}else if (_contentTpye === contentTpyeArrs[2]){//增加上传文件模式
        _data = data
    }
    return ajax.post(url, _data, {
        headers: {
            'Content-Type': _contentTpye
        }
    });
}

export {
    getAction,
    postAction
}