import React from 'react';
import './index.css'
import {Button} from 'antd-mobile'
function ErrorPage () {
    function logout(){
        window.sessionStorage.clear();
        window.location.href = window.location.origin;
    }
    return (
        <div className="item part13" style={{position:'relative'}}>
            <Button 
                onClick={logout}
                type='ghost' 
                inline 
                style={{
                    position:'absolute',
                    borderRadius:5,
                    border:'1px solid rgba(128,195,229,1)',
                    color:'rgba(128,195,229,1)',
                    left:'50%',
                    transform:'translate(-50%,0)',
                    bottom:'8%'
                }}
                >
                    好的，知道了
            </Button>
        </div>
    )
}

export default ErrorPage