import React,{Component} from 'react';
import './index.less'
import Boy from './Boy'
import Gril from './Gril'
export default class Part3 extends Component {
    render() {
        return (
            <div className='part-common part3 item'>
                <div className='boy'>
                    <Boy data={this.props.boy}/>
                </div>
                <div className='gril'>
                    <Gril data={this.props.gril}/>
                </div>
            </div>
        )
    }
}

