import React,{Component} from 'react';
import './index.less'
export default class Part6 extends Component {
    render() {
        return (
            <div className='part-common part6 item'>
                <div className='one'>
                    {this.props.data[0].surnames} <span>{this.props.data[0].num}人</span>
                </div>
                <div className='two'>
                {this.props.data[1].surnames} <span>{this.props.data[1].num}人</span>
                </div>
                <div className='three'>
                {this.props.data[2].surnames} <span>{this.props.data[2].num}人</span>
                </div>
                <div className='four'>
                {this.props.data[3].surnames} <span>{this.props.data[3].num}人</span>
                </div>
                <div className='five'>
                {this.props.data[4].surnames} <span>{this.props.data[4].num}人</span>
                </div>
                <div className='six'>
                {this.props.data[5].surnames} <span>{this.props.data[5].num}人</span>
                </div>
            </div>
        )
    }
}

