import React,{Component} from 'react';
import './index.less'
export default class Part2 extends Component {
    state={
        boy:1234,
        gril:1000,
        boyPercent:'54%',
        grilPercent:'46%',
        ratio:'1:1'
    }
    render() {
        let data=this.props.data,boy=data.find(item=>item.gender === '男'),
        gril=data.find(item=>item.gender === '女')
        let boyNum=boy.num || 0,grilNum=gril.num || 0
        let boyPercent=(boyNum/(boyNum+grilNum)).toFixed(3)*100,
        grilPercent=100-boyPercent
        let ratio=(grilNum/boyNum).toFixed(0)
        // console.log(ratio)
        return (
            <div className='part-common part2 item'>
                <div className='boy part2-font'>
                    男：{boyNum}人
                </div>
                <div className='boy-percent part2-font'>
                    {boyPercent}%
                </div>
                <div className='gril part2-font'>
                    女：{grilNum}人
                </div>
                <div className='gril-percent part2-font'>
                    {grilPercent}%
                </div>
                <div className='ratio part2-font'>
                    1:
                    {
                        ratio
                    }
                </div>
            </div>
        )
    }
}

