import React,{Component} from 'react';
import echarts from 'echarts';
export default class Ratio extends Component {
    state = {
        myChart:null,
        data:[{
            'name': 'TV',
            'value': 675
        }, {
            'name': '盒子高级版',
            'value': 1397
        }, {
            'name': '盒子基础版',
            'value': 318
        }, {
            'name': 'APP',
            'value': 101
        }, {
            'name': '小程序',
            'value': 110
        }, {
            'name': 'iPad',
            'value': 111
        }, {
            'name': 'PC',
            'value': 110
        }]
    }
    componentDidMount(){
        let chart = echarts.init(document.getElementById('ratio1'));
        this.setState({
            myChart:chart
        },()=>{
            this.setChartOption();
        })
    }
    setChartOption=()=>{
        let data=this.props.data.map(item=>{
            return {
                name:item.bornYear,
                value:item.num
            }
        })
        let option = {
            color: ['#955EE2', '#4ACAC9', '#82DCBC', '#56C972', '#A9DC80', '#F8D149', '#F2637A'],
            
            grid: {
                left: 0,
                right: 0,
                bottom: 0,
                top:0,
            },
            // legend: {
            //     bottom: '-2%',
            //     textStyle: {
            //         color: '#000',
            //         fontSize: 8,
        
            //     },
            //     icon: 'roundRect',
            //     data: data,
            // },
            tooltip: {
                formatter: "{a}：<br/>{b}: {c}人"
            },
            series: [
                // 主要展示层的
                {
                    radius: ['30%', '45%'],
                    center: ['50%', '50%'],
                    type: 'pie',
                    label: {
                        normal: {
                            show: true,
                            formatter: "{b}年：{c}人",
                            textStyle: {
                                fontSize: 12,
        
                            },
                            position: 'outside'
                        },
                        emphasis: {
                            show: true
                        }
                    },
                    labelLine: {
                        normal: {
                            show: true,
                            length: 10,
                            length2: 10
                        },
                        emphasis: {
                            show: true
                        }
                    },
                    name: "信息统计",
                    data:data,
        
                },
                
            ]
        };
        this.state.myChart.setOption(option); 
    }
    render() {
        return (
            <div id='ratio1' style={{height:'300px',width:'355px'}}>
                
            </div>
        )
    }
}

