import React from 'react';
import { Carousel } from 'antd-mobile';
import ErrorPage from './ErrorPage';
import Part1 from './Part1';
import Part2 from './Part2';
import Part3 from './Part3';
import Part4 from './Part4'; 
import Part5 from './Part5';
import Part6 from './Part6';
import Part7 from './Part7';
import Music from './bg.mp3'
import './index.css';
class Content extends React.Component {
    state={
        music:true
    }
    audioAutoPlay=(id)=>{
        var audio = document.getElementById(id),
　　　　play = function(){
　　　　　　audio.play();
　　　　　　document.removeEventListener("touchstart",play, false);
　　　　};
    　　audio.play();
    　　document.addEventListener("WeixinJSBridgeReady", function () {//微信
    　　　　play();
    　　}, false);
    　　document.addEventListener('YixinJSBridgeReady', function() {//易信
    　　　　play();
    　　}, false);
    　　document.addEventListener("touchstart",play, false);
    }
    componentDidMount() {
        // 开始自动播放
        this.audioAutoPlay('music')
    }
    toggleSound=()=>{
        if (!this.node.paused) {
            document.getElementById('music').pause();// 暂停
        } else {
            document.getElementById('music').play();// 播放
        }
        this.setState({
            music:!this.state.music
        })        
    }
    
    render() {
        let appInfo = window.sessionStorage.getItem('auth');
        appInfo = appInfo ? JSON.parse(appInfo) : null;
        if (appInfo) {
            return (
                <div className='my-carousel-wrapper'>
                    <span className={`sound ${this.state.music?'active':''}`} onClick={this.toggleSound}></span>
                    <audio  ref={node=>this.node=node} src={Music} controls="controls" id="music" hidden></audio>
                    <Carousel className="my-carousel"
                        vertical
                        dots={false}
                        // infinite
                    >
                        <Part1 />
						<Part2 data={appInfo.bgNumAllSchool}/>
                        
                        <Part3 boy={appInfo.boyMostCollege} gril={appInfo.girlMostCollege}/>
                        <Part4 data={appInfo.ageAnalyze}/>
                        <Part5 data={appInfo.nationAnalyze}/>
                        <Part6 data={appInfo.surnamesAnalyze}/>
                        <Part7 data={appInfo.constellationAnalyze}/>
                    </Carousel>
                    <i className="iconfont icon-xiala"></i>
                </div>
            )
        } else {
            return (
                <ErrorPage />
            )
        }
    }
}

export default Content