import React,{Component} from 'react';
import './index.less'
import Ratio from './Ratio'
export default class Part7 extends Component {
    render() {
        return (
            <div className='part-common part7 item'>
                <div className='text-wrapper'>
                    <div className='text'>
    {this.props.data[0].constellation}：以总数为<span className='part7-font'>{this.props.data[0].num}</span>人荣登榜首
                    </div>
                    <div className='text'>
        {this.props.data[1].constellation}：以总数为<span className='part7-font'>{this.props.data[1].num}</span>人位居第二
                    </div>
                    <div className='text'>
        {this.props.data[2].constellation}：以总数为<span className='part7-font'>{this.props.data[2].num}</span>人跻身第三
                    </div>
                    
                </div>
                <div className='chart'>
                    <Ratio data={this.props.data}/>
                </div>
            </div>
        )
    }
}

