import React,{Component} from 'react';
import './index.less'
import Ratio from './Ratio'
export default class Part4 extends Component {
    render() {
        return (
            <div className='part-common part4 item'>
                <div className='large'>
                    最大新生：
                    {/* <span>1996年</span> */}
                    <span>{this.props.data.maxAge}岁</span>
                    {/* <span>5名同学</span> */}
                </div>
                <div className='little'>
                    最小新生：
                    {/* <span>1996年</span> */}
                    <span>{this.props.data.minAge}岁</span>
                    {/* <span>5名同学</span> */}
                </div>
                <div className='chart'>
                    <Ratio data={this.props.data.ageAnalyzeInfo}/>
                </div>
            </div>
        )
    }
}

