import React,{Component} from 'react';
import './index.less'
import Ratio from './Ratio'
export default class Part5 extends Component {
    render() {
        return (
            <div className='part-common part5 item'>
                <div className='text-wrapper'>
                    <div className='text'>
        2020级萌新中一共有<span>{this.props.data.length}</span>个民族，
                    </div>
                    <div className='text'>
        除了汉族的<span>{this.props.data[0].num}人</span>以外，
                    </div>
                    <div className='text'>
        最多的就是<span>{this.props.data[1].nation}</span>啦！
                    </div>
                    <div className='text'>
        其次是<span>{this.props.data[2].nation}、{this.props.data[3].nation}、{this.props.data[4].nation}</span>
                    </div>
                    <div className='text'>
                        人数相对较多。
                    </div>
                </div>
                <div className='chart'>
                    <Ratio data={this.props.data}/>
                </div>
            </div>
        )
    }
}

