import React,{Component} from 'react';
import echarts from 'echarts';
export default class Gril extends Component {
    state = {
        myChart:null,
        data:[
            {
                name:'学院1',
                value:180
            },
            {
                name:'学院2',
                value:280
            },
            {
                name:'学院3',
                value:380
            }
        ]
    }
    componentDidMount(){
        let chart = echarts.init(document.getElementById('gril-chart'));
        this.setState({
            myChart:chart
        },()=>{
            this.setChartOption();
        })
    }
    setChartOption=()=>{
        let option={
            color: ['#F5D300'],
            tooltip: {
                trigger: 'axis',
                axisPointer: {            // 坐标轴指示器，坐标轴触发有效
                    type: 'shadow'        // 默认为直线，可选为：'line' | 'shadow'
                }
            },
            grid: {
                left: '3%',
                right: '4%',
                bottom: '3%',
                top:'6%',
                containLabel: true
            },
            xAxis: [
                {
                    type: 'category',
                    data: this.props.data.map(item=>item.collegeName),
                    axisTick: {
                        alignWithLabel: true
                    },
                    
                    axisLabel: {
                        show: true,
                        textStyle: {
                            color: '#666',  //更改坐标轴文字颜色
                            fontSize : 8      //更改坐标轴文字大小
                        },
                        rotate:-10
                    },
                }
            ],
            yAxis: [
                {
                    type: 'value',
                    axisLine:{
                        show:false
                    },
                    splitLine:{
                        show:true,
                        lineStyle:{
                            type:'dashed'
                        }
                    },
                    axisTick: {
                        show: false
                    },
                    axisLabel: {
                        show: true,
                        textStyle: {
                            color: '#666',  //更改坐标轴文字颜色
                            fontSize : 8      //更改坐标轴文字大小
                        }
                    }
                }
            ],
            series: [
                {
                    name: '人数',
                    type: 'bar',
                    data: this.props.data.map(item=>item.num)
                }
            ]
        }
        this.state.myChart.setOption(option); 
    }
    render() {
        return (
            <div className='gril-chart' id='gril-chart' style={{height:'158px',width:'252px'}}>
                
            </div>
        )
    }
}

